import "./scss/ShopButton.scss";
const ShopButton = ({ CardStyle, btnClass }) => {
  const shopBtnStyle = { fontFamily: CardStyle?.style?.font }
  return <div className={`tb_shop_btn_wrap ${btnClass}`}>
      <div className={`tb_shop_btn${CardStyle.style.mode ? ' tb_shop_btn_light' : ' tb_shop_btn_dark'}`} style={shopBtnStyle}>      {CardStyle.shopText}
    </div>
  </div>
}


export default ShopButton;