import React from 'react';
import Card from './Elements/Card';

const CollageRows = ({ CardStyle, ThemeStyle, ThemeInfo, largeDiv, smallDiv, Post, completeDataObject, index, clickToShowPopUp, postSizeInPx, postSize, onClickPopUpSlider, ProductSetting }) => {
    const largeDivStyle = {
        width: `${largeDiv}%`
    };

    const smallDivStyle = {
        width: `${smallDiv}%`
    };

    if (!Post || Post.length === 0) return null;

    return (
        <div className={`ts_cs_post_row ${ThemeStyle.direction == 'right' ? 'ts_cs_row_right' : 'ts_cs_row_left'}`}>
            <div className="ts_cs_wrap_single" style={largeDivStyle}>
                <Card Post={completeDataObject[Post[0]]} key={`sqr_crd_0_${completeDataObject[Post[0]].id}`} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} index={index} postSize={100} clickToShowPopUp={clickToShowPopUp} postSizeInPx={postSizeInPx*2} onClickPopUpSlider={onClickPopUpSlider} />
            </div>
            <div className="ts_cs_wrap_multi" style={smallDivStyle}>
                {Post.map((item, idx) => {
                    if (idx === 0) return null; // Skip the first item since it's rendered in the large div
                    const cardData = completeDataObject[item];
                    return (
                        <Card Post={cardData} key={`sqr_crd_${idx}_${item.id}`}  ProductSetting={ProductSetting}  CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} index={idx} postSize={postSize} clickToShowPopUp={clickToShowPopUp} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} />
                    );
                })}
            </div>
        </div>
    );
};

export default CollageRows;