import React, { PureComponent } from "react";
import "./scss/CollageTheme.scss";
import CollageRows from "./CollageRows";
import { getColumnCount, getSizeInPercent } from "../../../utils";

export const getCollageColumns = (column) => {
  switch (column) {
    case 3: return 3;
    case 5: return 4;
    case 7: return 5;
    case 9: return 6;
    default: return 1;
  }
}
class CollageTheme extends PureComponent {
  componentDidMount() {
    this.props.loadBranding()
  }

  render() {
    const { postData, completeDataObject, CardStyle, ThemeStyle, ProductSetting, ThemeInfo, clickToShowPopUp, onClickToCTA, onClickPopUpSlider, containerSize } = this.props;
    const { desktop, mobile } = ThemeStyle.column
    const minCardSize = 300;
    const desktopColumn = containerSize.width > 767 ? desktop : mobile ? mobile : 3
    const columns =  getColumnCount(containerSize.width, ThemeStyle.column, minCardSize);
    const postSize = getSizeInPercent(containerSize.width, getCollageColumns(desktopColumn))

    var i = 0,
    totalPost = postData.length, 
    newPostdata = [];
    while (i < totalPost) {
      newPostdata.push(postData.slice(i, i += desktopColumn));
    }
    return (
      <div tabIndex="0" aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed" className="tb_ct_post_container">
        {newPostdata && newPostdata.length > 0 && newPostdata.map((item, index) => {
          return item.length == desktopColumn ? <CollageRows CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} ThemeInfo={ThemeInfo} ownerId={ThemeInfo.ownerId} Post={item} key={`sqr_crd_${index}_${item.id}`} index={index} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} postSizeInPx={containerSize.width / columns} completeDataObject={completeDataObject} largeDiv={postSize * 2} smallDiv={100 - (postSize * 2)} postSize={100 / (desktopColumn - 1) * 2} onClickPopUpSlider={onClickPopUpSlider} /> : null
        })}
      </div>
    )
  }
}

export default CollageTheme